// 我们组件
<template>
  <div class="us">
    <h1>这里是我们的组件</h1>
    <div class="wrap">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="image in images" :key="image">
            <img :src="'../../static/images/'+image" alt=""/>
          </div>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </div>
  </div>
</template>

<script>
// 引入swiper
import Swiper from "swiper";

// import 'swiper/dist/css/swiper.min.css'

export default {
  name: "us",
  data() {
    return {
      images: ["a1.jpg", "a2.jpeg", "a3.jpg", "a4.jpg", "a5.jpeg"]
    };
  },
  // 钩子函数
  mounted() {
    var swiper = new Swiper(".swiper-container", {
      pagination: {
        el: ".swiper-pagination"
      },
      effect : 'fade',
      loop:true,
      speed:3000,
      //  direction : 'vertical',
      autoplay: {
        delay: 1000,
        stopOnLastSlide: true,
        disableOnInteraction: true,
         waitForTransition: true,
      },
    });
    // 这里更改分页器的大小
    // var buttons = document.getElementsByClassName("swiper-pagination-bullet");
    // for(var i = 0;i<buttons.length;i++){
    //   buttons[i].style.width = "0.5rem";
    //   buttons[i].style.height = "0.5rem";
    // }
  }
};
</script>

// <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../assets/sass/base.scss";
// swiper 
@import "../../node_modules/swiper/dist/css/swiper.min.css";

.us {
  margin-top: rem(150);
  h1 {
    font-size: rem(50);
    text-align: center;
    line-height: rem(150);
  }
  .wrap{
  width: rem(710);
  margin: 0 auto;
  border-radius: rem(20);
    img {
    width: rem(710);
    display: block;
    height: rem(500);
    // border-radius: rem(20);
    }
    .swiper-pagination-bullet{
      background: red!important;
      width: rem(100)!important;
      height: rem(100)!important;
    }
  }
}
</style>



