// 精华组件
<template>
  <div class="goods">
    <!-- 使用主题主件，传递参数，添加属性 -->
    <topics-list :topicss="topics"></topics-list>
    <!-- 没有数据了的提示信息 -->
    <div class="tipsinfo" v-show="tipsinfo">已经到底了！</div>
    <!-- 使用下拉刷新组件 -->
    <infinite-loading @infinite="infiniteHandler" v-if="showloading"></infinite-loading>
  </div>
</template>

<script>
// 引入主题主件
import TopicsList from "@/components/topics/topicslist/TopicsList.vue";
// 引入主题请求数据模块
import { gettopics } from "@/getdata/gettopics.js";
// 引入下拉刷新模块
import InfiniteLoading from "vue-infinite-loading";

export default {
  name: "goods",
  data() {
    return {
      // 放数据的数组
      topics: [],
      // 当前的页码
      page: 1,
      // 控制没有数据了的提示信息显示的变量
      tipsinfo:false,
      // 控制刷新组件显示的变量
      showloading:false,
    };
  },
  // 加载主件
  components: {
    // 主题模块
    TopicsList,
    // 刷新组件
    InfiniteLoading
  },
  // 钩子函数
  mounted() {
    // 请求数据
    gettopics({ tab: "good"}).then(data => {
      // 数据
      this.topics = data.data.data;
      // 下拉刷新的变量
      this.showloading = true;
    });
  },
  methods: {
    // 下拉刷新组件的事件处理程序
    infiniteHandler($state) {
      // 避免多次请求
      setTimeout(() => {
        // 请求下一页数据
        gettopics({ tab:"good",page: ++this.page}).then(data => {
          // 数组连接
          // this.topics = this.topics.concat(data.data.data);
          for(var i = 0;i<data.data.data.length;i++){
            this.topics.push(data.data.data[i]);
          }
          // console.log(data.data.data)
          // 判断
          if(data.data.data == 0){
            // 提示信息显示
            this.tipsinfo = true;
            // 刷新隐藏
            this.showloading = false;
          }
        });
        // 调用infiniteHandler
        $state.loaded();
      }, 1000);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../../assets/sass/base.scss";
.goods {
  margin-bottom: rem(60);
  .tipsinfo {
    text-align: center;
    color: blue;
    font-size: rem(20);
    line-height: rem(80);
  }
}
</style>



