// 主题详情内容组件
<template>
  <div class="show">
      <h1 class="title">{{title}}</h1>
      <div class="info">
        作者:<span class="loginname">{{loginname}}</span>
        -来自:<span class="tab">{{tabs[tab]}}</span>
        -发布于:<span class="create_at">{{getcreate_at}}</span><br>
        浏览次数:<span class="visit_count">{{visit_count}}</span>
        最后一次编辑是:<span class="last_reply_at">{{getlast_reply_at}}</span>
      </div>
      <div v-html="content" class="content"></div>
      <div class="repliesnum" style="color:green">{{replies.length}}条回复</div>
      <ul class="repliescontent" v-for="(replie,index) in replies">
        <li>
          <img :src="replie.author.avatar_url" alt="">
            <div class="replieinfo">
              <span class="loginname">{{replie.author.loginname}}</span>
              <span>{{index+1}}</span>楼
              <span>{{getreplies}}</span>
              <div v-html="replie.content" class="content"></div>
            </div>  
        </li>
      </ul>
  </div>
</template>

<script>
// 引入axios模块(封装的ajax)
import axios from "axios";
// 引入主题请求数据模块
import {gettopic} from "@/getdata/gettopics.js";

export default {
  name: "show",
  data() {
    return {
      replies:[],
      title:"",
      loginname:"",
      tab:"",
      content:"",
      // 发布日期
      create_at:"",
      // 浏览次数
      visit_count:"",
      // 最后一次编辑
      last_reply_at:"",
      // 评论时间
      repliestime:"",

      tabs:{
        ask:"问答",
        weex:"weex",
        share:"分享",
        job:"招聘",
      },
    };
  },
  // 钩子函数
  mounted() {
    var id = this.$route.params.id;
    // 请求数据
    gettopic(id).then((data)=>{
      // console.log(this.$route)
      // console.log(data.data.data.replies.content)
      this.replies = data.data.data.replies;
      // this.repliestime = data.data.data.replies.create_at
      this.title = data.data.data.title;
      this.loginname = data.data.data.author.loginname;
      this.tab = data.data.data.tab;
      this.content = data.data.data.content;
      // 发布日期
      this.create_at = data.data.data.create_at;
      // 浏览次数
      this.visit_count = data.data.data.visit_count;
      // 最后一次编辑
      this.last_reply_at = data.data.data.last_reply_at;
    
    });
  },
  
  methods:{
  },
  computed:{
    getcreate_at:function(){
    var  timestr = this.create_at.slice(0,10);
    var n  = new Date().getTime();
    var e = new Date(timestr).getTime();
    var time = Math.floor((n-e)/1000);
    if(time/60>1 && time/60<60){
      time = Math.floor(time/60)+"分钟前"
      }
    else if(time/60/60>1 && time/60/60<24){
      time = Math.floor(time/60/60)+"小时前"
    }
    else if(time/60/60/24>1 && time/60/60/24<30){
      time = Math.floor(time/60/60/24)+"天前"
    }
    else if(time/60/60/24/30>1 && time/60/60/24/30<12){
      time = Math.floor(time/60/60/24/30)+"个月前"
    }
    else if(time/60/60/24/30/12>1){
      time = Math.floor(time/60/60/24/30/12)+"年前"
    }
    else{
      time = "刚刚"
    }
    return time;
    },
    getlast_reply_at:function(){
    var  timestr = this.last_reply_at.slice(0,10);
    var n  = new Date().getTime();
    var e = new Date(timestr).getTime();
    var time = Math.floor((n-e)/1000);
    if(time/60>1 && time/60<60){
      time = Math.floor(time/60)+"分钟前"
      }
    else if(time/60/60>1 && time/60/60<24){
      time = Math.floor(time/60/60)+"小时前"
    }
    else if(time/60/60/24>1 && time/60/60/24<30){
      time = Math.floor(time/60/60/24)+"天前"
    }
    else if(time/60/60/24/30>1 && time/60/60/24/30<12){
      time = Math.floor(time/60/60/24/30)+"个月前"
    }
    else if(time/60/60/24/30/12>1){
      time = Math.floor(time/60/60/24/30/12)+"年前"
    }
    else{
      time = "刚刚"
    }
    return time;
    },
    getreplies:function(){
      for(var i = 0 ;i<this.replies.length;i++){
        var timestr =  this.replies[i].create_at.slice(0,10);
        var n  = new Date().getTime();
        var e = new Date(timestr).getTime();
        var time = Math.floor((n-e)/1000);
        if(time/60>1 && time/60<60){
          time = Math.floor(time/60)+"分钟前"
          }
        else if(time/60/60>1 && time/60/60<24){
          time = Math.floor(time/60/60)+"小时前"
        }
        else if(time/60/60/24>1 && time/60/60/24<30){
          time = Math.floor(time/60/60/24)+"天前"
        }
        else if(time/60/60/24/30>1 && time/60/60/24/30<12){
          time = Math.floor(time/60/60/24/30)+"个月前"
        }
        else if(time/60/60/24/30/12>1){
          time = Math.floor(time/60/60/24/30/12)+"年前"
        }
        else{
          time = "刚刚"
        }
      }  
      return time;
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../../assets/sass/base.scss";
.show{
  width: 10rem;
  margin-top:rem(230);
  margin-bottom:rem(80);
  font-size: rem(25);
  line-height: rem(35);
  .info{
      text-align: center;
      font-size: rem(20);
      .loginname{
        color:hotpink;
      }
      .tab{
        color: aqua;
      }
      .create_at{
        color: red;
      }
      .visit_count{
        color: green;
        background: yellow;
      }
      .last_reply_at{
        color: blue;
      }
  }
  .title{
    padding: rem(15);
    font-size: rem(35);
    line-height:rem(45);
    font-weight: bold;
  }
  .content{
  width: rem(710);
  margin: 0 auto;
  margin-top: rem(20);
}
/deep/ img{
  width: 100%;
}
.repliesnum{width: rem(710);margin: 0 auto;margin-top: rem(50)}
.repliescontent{
  width: rem(710);
  margin: 0 auto;
  li{
    display: flex;
    margin: rem(20) 0;
    .replieinfo{
      flex: 1;
      margin-left: rem(20);
      font-size: rem(20);
    }
     img{
      width: rem(80);
      height: rem(80);
    }
    .content{
      font-size: rem(25)
    }
  }
}
}
</style>


