// 主题主件，在这里渲染
<template>
  <div class="topics">
    <!-- <h1>{{topicss}}</h1> -->
    <!-- 判断显示菊花 -->
    <Loading v-if="!topicss.length"></Loading>
    <!-- 循环渲染数据 -->
    <div class="alltopics" v-for="topic in topicss">
      <!-- 添加src属性渲染头像 -->
      <img v-lazy="topic.author.avatar_url" alt />
      <div class="tab">
        <!-- 判断是否置顶 -->
        <div class="top" v-if="topic.top">置顶</div>
        <!-- 判断是否精选 -->
        <div v-else-if="topic.good" class="good">精选</div>
        <!-- 显示tab类型 -->
        <div v-else>{{tabs[topic.tab]}}</div>
      </div>
      <!-- 显示title内容 -->
      <div class="title">
          <router-link :to="'/index/show/'+topic.id" tag="div" class="titlecontent" v-html="topic.title"></router-link>
          <!-- <div class="create_at" v-model="create_attime=topic.create_at">{{getcreate_attime}}</div> -->
      </div>
    </div>
    <!-- 回到顶部 -->
    <transition name="gotop">
    <div class="gotop" @click="gotop" v-show="istopshow">
      <span class="iconfont iconxiangshang"></span>
    </div>
    </transition>
  </div>  
</template>

<script>

// 引入loading组件
import Loading from "@/components/topics/Loading.vue"

export default {
  name: "topics",
  data() {
    return {
      // 把tab类型换成文字
      tabs:{
        ask:"问答",
        job:"招聘",
        share:"分享",
        weex:"weex"
      },
      create_attime:"",
      // 滚动条top初始值
      scrollTop:0,
      // 控制回到顶部显示与隐藏
      istopshow:false,
    };
  },
  // 加载组件
  components:{
    Loading
  },
  // 计算属性
  computed: {
    getcreate_attime:function(){
      return this.create_attime.slice(0,10);
    }
  },
  // 接收属性数据
  props:['topicss'],
   // 钩子函数,模块挂载后调用
  mounted() {
    // 监听window窗口滚动条
    window.addEventListener("scroll",this.myscroll)
  },
  // 钩子函数，实例销毁后调用
  destroyed() {
    window.removeEventListener("scroll",this.myscroll)
  },
  // 方法
  methods: {
    // 点击置顶按钮的事件处理程序
    gotop:function(){
      // console.log(document.documentElement.scrollTop)
      // document.documentElement.scrollTop = 0;
      // console.log(this.scrollTop)
      // 每次减少的步长
       let step = Math.floor(-this.scrollTop/10)
      //  滚动条top值逐渐减少
         let timer = setInterval(()=>{
            document.documentElement.scrollTop =
            document.body.scrollTop = 
             this.scrollTop+step;
            // 判断是否已经回到了顶部
            if(document.documentElement.scrollTop<1){
              // 回到顶部了，释放定时器吧
                clearInterval(timer);
          }
      },1);
    },
    // 窗口滚动条事件处理程序
    myscroll:function(){
      // 获取当前滚动条top值,
      this.scrollTop = document.documentElement.scrollTop ||
      document.body.scrollTop ||
      window.scrollTop ||
      window.pageYOffset;
      // console.log(this.scrollTop)
      // 回到顶部按钮显示与隐藏
      if(this.scrollTop>200){
        this.istopshow = true;
      }
      else {
        this.istopshow = false;
      }
    }
  },
 
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../../../assets/sass/base.scss";
// 引入主题样式
@import "../../../assets/sass/topicslist.scss";

.topics{
  margin-top:rem(230);
  .title{
    &:active{color: red;}
  }
  .gotop{
    width: rem(50);
    height: rem(50);
    border: rem(3) solid blue ;
    border-radius: 50%;
    position: fixed;
    bottom: rem(100);
    right: rem(10);
    z-index: 9;
    span{
          font-size: rem(50);
          color: red;
    }

  }
};
.gotop-enter{
  opacity: 0;
}
.gotop-enter-active{
  transition: all 2s
}
.gotop-leave-to{
  opacity: 0;
}
.gotop-leave-active{
  transition: all 2s
}
</style>



