// 问答组件
<template>
  <div class="ask">
    <!-- 使用主题主件，传递参数，添加属性 -->
    <topics-list :topicss="topics"></topics-list>
    <!-- 没有数据了的提示信息 -->
    <div class="tipsinfo" v-show="tipsinfo">已经到底了！</div>
    <!-- 使用下拉刷新组件 -->
    <infinite-loading @infinite="infiniteHandler" v-if="showloading"></infinite-loading>
  </div>
</template>

<script>
// 引入主题主件
import TopicsList from "@/components/topics/topicslist/TopicsList";
// 引入主题请求数据的模块
import {gettopics} from "@/getdata/gettopics.js";
// 引入下拉刷新组件
import InfiniteLoading from "vue-infinite-loading";

export default {
  name: "ask",
  data() {
    return {
      // 请求数据数组
      topics: [],
      // 页码
      page:1,
      // 提示信息
      tipsinfo:false,
      // 下拉刷新
      showloading:false,
    };
  },
  // 加载主件
  components:{
    // 主题
    TopicsList,
    // 下拉刷新
    InfiniteLoading,
  },
  // 钩子函数
  mounted() {
    // 请求数据
     gettopics({tab:"ask",}).then((data)=>{
      //  数据
      this.topics = data.data.data;
      // 刷新的变量
      this.showloading = true;
    });
  },
  methods:{
    // 事件
     infiniteHandler($state) {
      // 避免多次请求
      setTimeout(() => {
        // 请求下一页数据
        gettopics({ tab: "ask", page: ++this.page }).then(data => {
          // 数组连接
          this.topics = this.topics.concat(data.data.data);
          // console.log(data.data.data)
          if(data.data.data ==0){
            // 提示信息
            this.tipsinfo = true;
            // 刷新
            this.showloading = false;
          }

        });
        // 调用infiniteHandler
        $state.loaded();
      }, 1000);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../../assets/sass/base.scss";
    .ask{
      margin-bottom:rem(60);
    .tipsinfo{
    text-align: center;
    color: blue;
    font-size: rem(20);
    line-height: rem(80);
  }
    }
</style>



