// weex组件
<template>
  <div class="weex">
      <!-- 使用主题主件，传递参数，添加属性 -->
      <topics-list :topicss="topics"></topics-list>
      <!-- 没有数据了的提示信息 -->
      <div class="tipsinfo" v-if="showtips">已经到底了！</div>
      <!-- 使用下拉刷新组件 -->
    <infinite-loading @infinite="infiniteHandler" v-if="showloading"></infinite-loading>
  </div>
</template>

<script>
// 引入主题主件
import TopicsList from "@/components/topics/topicslist/TopicsList";
// 引入主题请求数据模块
import {gettopics} from "@/getdata/gettopics.js";
// 引入下拉刷新模块
import InfiniteLoading from "vue-infinite-loading";

export default {
  name: "weex",
  data() {
    return {
      // 数据
      topics: [],
      // 页码
      page:1,
      // 提示信息呢
      showtips:false,
      // 刷新页面
      showloading:false,
    };
  },
  // 加载主题主件
  components:{
    // 主题主件
    TopicsList,
    // 下拉刷新组件
     InfiniteLoading
  },
  // 钩子函数
  mounted() {
    // 箭头函数
    gettopics({tab:"weex",}).then((data)=>{
      // 数据
      this.topics = data.data.data;
      // 刷线页面
      this.showloading = true;
    });
    // var loadings = document.getElementsByClassName("loading-default");
    // for(var i = 0;i<loadings.length;i++){
    //   loadings[i].style.width = "15px";
    //   loadings[i].style.height = "15px";
    // }
  },
  methods:{
     // 事件
    infiniteHandler($state) {
      // 避免多次请求
      setTimeout(() => {
        // 请求数据
        gettopics({ tab: "weex", page: ++this.page }).then(data => {
          // 数组连接
          this.topics = this.topics.concat(data.data.data);
          // console.log(data.data.data)
          if(data.data.data == 0){
            // 提示信息显示
              this.showtips = true;
              // 刷新隐藏
              this.showloading = false;
          }
        });
        // 调用infiniteHandler
        $state.loaded();
      }, 1000);
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../../assets/sass/base.scss";
.weex {
  margin-bottom: rem(60);
   .tipsinfo{
    text-align: center;
    color: blue;
    font-size: rem(20);
    line-height: rem(80);
  }
}
</style>


