import Vue from 'vue'
import Router from 'vue-router'
// 一级导航组件
import Index from "@/components/Index"
import Code from "@/components/Code"
import Us from "@/components/Us"
// 二级导航组件
import Ask from "@/components/topics/Ask.vue"
import Job from "@/components/topics/Job.vue"
import Share from "@/components/topics/Share.vue"
import Good from "@/components/topics/Good.vue"
import Weex from "@/components/topics/Weex.vue"
import All from "@/components/topics/All.vue"
// 点击主题显示的内容主件
import Show from "@/components/topics/Show.vue"


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index,
      children:[
        {
          path: '/',
          name: 'all',
          component: All,
        },
        {
          path: 'good',
          name: 'Good',
          component: Good,
        },
        {
          path: 'weex',
          name: 'Weex',
          component: Weex,
        },
        {
          path: 'share',
          name: 'Share',
          component: Share,
        },
        {
          path: 'job',
          name: 'Job',
          component: Job,
        },
        {
          path: 'ask',
          name: 'Ask',
          component: Ask,
        },
        {
          path: 'show/:id',
          name: 'Show',
          component: Show,
        }
      ]
    },
    {
      path: '/code',
      name: 'Code',
      component: Code,
    },
    {
      path: '/us',
      name: 'Us',
      component: Us,
    },
    {
      path:"/*",
      redirect:"/index"
    }
  ]
})



