// 首页顶部导航全部选项的组件
<template>
  <div class="all">
    <!-- 使用主题主件，传递参数，添加属性 -->
    <topics-list :topicss="topics"></topics-list>
    <!-- 没有数据了的提示信息 -->
    <div class="tipsinfo" v-if="tipsinfo">已经到底了！</div>
    <!-- 使用下拉刷新组件 -->
    <infinite-loading @infinite="infiniteHandler" v-if="showloading"></infinite-loading>
  </div>
</template>

<script>
// 引入主题主件
import TopicsList from "@/components/topics/topicslist/TopicsList.vue";
// 引入主题请求数据模块
import { gettopics } from "@/getdata/gettopics.js";
// 引入下拉刷新模块
import InfiniteLoading from "vue-infinite-loading";

export default {
  name: "all",
  data() {
    return {
      // 声明数组放请求到的数据，用于数据双向绑定
      topics: [],
      // 初始页码
      page:1,
      // 没有数据了提示信息的变量
      tipsinfo:false,
      // 下拉刷新组件的变量
      showloading:false,
    };
  },
  // 挂载组件
  components: {
    //  主题组件
    TopicsList,
    // 下拉刷新组件
    InfiniteLoading,
  },
  // 钩子函数
  mounted() {
    // 请求数据
    gettopics({tab:"all",}).then(data => {
      // 取数据
      this.topics = data.data.data;
      // 刷新组件显示
      this.showloading = true;
    });
  },
  // 方法
  methods: {
    // 下拉刷新组件的事件处理程序
    infiniteHandler($state) {
      // 避免多次请求设置一次性定时器
      setTimeout(() => {
        // 请求下一页数据
        gettopics({page: ++this.page,limit:20}).then(data => {
          // 把两次请求的数组数据连接起来
          this.topics = this.topics.concat(data.data.data);
          // 判断下一页的数据长度是否为0（已经没有数据了），
          if(data.data.data == 0){
            // 让没有数据了的提示信息显示
            this.tipsinfo = true;
            // 让刷新的组件隐藏
            this.showloading = false;
          }
        });
        // 触发 infiniteHandler 再次调用
        $state.loaded();
      }, 2000);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../../assets/sass/base.scss";
.all {
  margin-bottom: rem(60);
  .tipsinfo{
    text-align: center;
    color: blue;
    font-size: rem(20);
    line-height: rem(80);
  }
}
</style>



