// 引入axios模块(封装的ajax)
import axios from "axios";
// 配置axios的基本信息
var $http = axios.create({
    // 请求主题的路径的基础url
    baseURL: 'https://www.vue-js.com/api',
    // 请求时间
    timeout: 5000,
    // 头信息
    headers: {'X-Custom-Header': 'foobar'}
  });

// 主题列表请求数据
function gettopics(option){
  // 参数传个对象
  var newpotion = Object.assign({
    // 默认值
      tab:"all",
      page:1,
      limit:10,
  },option);
    // 返回
    return $http({
        // 请求主题的url 拼接到上面基础路径
        url: '/v1/topics?',
        // 请求方式
        method: 'get',
        // 参数
        params:newpotion,
      })
};

// 主题内容请求数据
function gettopic(id){
  return $http.get('v1/topic/'+id);
}
// 暴露模块
export {gettopics,gettopic};


