<template>
  <div class="index">
    <ul class="nav">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <router-link to="/index" tag="li">全部</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/good" tag="li">精选</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/weex" tag="li">weex</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/share" tag="li">分享</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/ask" tag="li">问答</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/index/job" tag="li">招聘</router-link>
          </div>

          <div class="swiper-slide">
            <router-link to="/a" tag="li">全部</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/a" tag="li">精选</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/a" tag="li">weex</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/a" tag="li">分享</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/a" tag="li">问答</router-link>
          </div>
          <div class="swiper-slide">
            <router-link to="/a" tag="li">招聘</router-link>
          </div>
        </div>
      </div>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
// swiper
import Swiper from "swiper";

export default {
  name: "index",
  data() {
    return {};
  },
  // 钩子函数
  mounted() {
    var swiper = new Swiper(".swiper-container", {
      slidesPerView: 6,
      spaceBetween: 10,
      pagination: {
        el: ".swiper-pagination",
        clickable: true
      },
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
// rem计算公式
@import "../assets/sass/base.scss";
// swiper
@import "../../node_modules/swiper/dist/css/swiper.min.css";
.nav {
  position: fixed;
  top: rem(150);
  width: 100%;
  height: rem(80);
  background: #ccc;
  color: #fff;
  display: flex;
  align-items: center;
  // justify-content: space-around;
  li {
    font-size: rem(25);
    padding: 0 rem(20);
    line-height: rem(45);
    text-align: center;
  }
  .router-link-exact-active {
    background: green;
    border-radius: rem(10);
  }
}
</style>



