// 这个是根主件，所有的页面都会显示
<template>
  <div id="app">
    <div class="topbanner">
         <span class="iconfont iconVue"></span>
         <p>Vue.js中文社区</p>
    </div>
    <!-- <div>{{$route.path}}</div> -->
    <!-- <div>{{routeName}}</div> -->
    <ul class="bottom">
      <!-- 判断是否选中状态 -->
      <!-- 添加class属性 -->
        <router-link to="/index" tag="li" :class="routeName=='index'?'active':''"><span class="iconfont" :class="routeName=='index'?'iconhomefill':'iconhome'"></span><p>首页</p></router-link>
        <router-link to="/code" tag="li" :class="routeName=='code'?'active':''"><span class="iconfont" :class="routeName=='code'?'iconlikefill':'iconlike'"></span><p>二维码</p></router-link>
        <router-link to="/us" tag="li" :class="routeName=='us'?'active':''"><span class="iconfont" :class="routeName=='us'?'iconroundcheckfill':'iconroundcheck'"></span><p>我们</p></router-link>
    </ul>
    <router-view/>
    
  </div>
</template>

<script>
export default {
  name: 'App',
  // 计算属性
  computed:{
    routeName:function(){
      // 分割字符串
    var arr = this.$route.path.split("/");
    // 返回值
    return arr[1];
    // console.log(arr)
  }
  }
}
</script>
<style lang="scss">
// rem计算公式
    @import "./assets/sass/base.scss";
    .topbanner{
      background: pink;
      width: 100%;
      height: rem(150);
      display: flex;
      align-items: center;
      justify-content: center;
      position: fixed;
      top:0;
      left:0;
      span{
         font-size: rem(150);
         color:green;
      }
     p{
        font-size: rem(50);
        color: red;
     }
    }
    .bottom{
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height:rem(80);
      background: #09c;
      display: flex;
      li{
        width: 33.33%;
        text-align: center;
        height:rem(80);
        span{
          font-size: rem(35);
           line-height: rem(50);
        }
        p{
          font-size: rem(25);
        }
      }
      .active{
         background: green;
        border-radius: rem(15);
        color: pink;
      }
      // .router-link-exact-active{
      //   background: green;
      //   border-radius: rem(15);
      //   color: pink;
      // }
    }

</style>



