import Vue from 'vue'
import App from './App'
// 引入路由模块
import router from './router'
// 引入移动端布局方案模块
import lib from "lib-flexible"
// 引入懒加载模块
import VueLazyload from "vue-lazyload"
// 配置懒加载
Vue.use(VueLazyload,{
  preLoad: 1.3,
  // 图片加载失败时替换的图片
  error:require("../static/images/user.jpg"),
  // 图片加载时显示的图片
  loading:require("../static/images/loadingimages.gif"),
  attempt:1
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



